.tab .nav-tabs li a:after {
	content: "";
	width: 0;
	height: 4px;
	background: black;
	border: 1px solid black;
	border-radius: 2px;
	position: absolute;
	bottom: 0;
	left: 0;
	opacity: 0;
	z-index: 1;
	-webkit-transition: all 1s ease 0s;
	transition: all 1s ease 0s;
}

.tab .nav-tabs li:hover a:after, .tab .nav-tabs li.active a:after {
	width: 100%;
	opacity: 1;
}

.tab .tab-content {
	padding: 15px 20px;
	margin-top: 20px;
	font-size: 16px;
	color: #fff;
	letter-spacing: 1px;
	line-height: 30px;
